import interfaceurl from '@/utils/interface.js'
import mpvuePicker from '@/components/mpvue-picker/mpvuePicker.vue';
import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
import cityData from '@/common/city.data.js';
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		mpvuePicker,
		mpvueCityPicker,
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '添加收货地址' //导航栏文字
			},
			true_name: '',
			cellphone: '',
			detail_address: '',
			address_id: '',
			//动态参数
			themeColor: '#007AFF',
			cityPickerValueDefault: [0, 0, 1],
			mode: '',
			deepLength: 1,
			pickerValueDefault: [0],
			pickerValueArray:[],
			info: {}, //用户地址信息，深拷贝
			position: '' ,//选择的地址
			saveParams: {
				regionId: 0,
				receiver: '',
				receiverAddr: '',
				receiverPhone: '',
				isDefault: 0, //0非默认，1默认
				addressId: ''
			},
			forceDefault: false //判断是否需要强制默认
		}
	},
	onLoad(options) {
		console.log('isDefault',options)
		if(options.isDefault == 'false') {
			this.forceDefault = true
			this.saveParams.isDefault = 1
		} else {
			this.forceDefault = false
		}
		if(options.item) {
			this.saveParams = JSON.parse(options.item)
			// this.address_id = item.addressId
			// this.true_name = item.receiver
			// this.cellphone = item.receiverPhone
			// this.detail_address = item.receiverAddr
		}
	},
	onShow() {
     
	},
	methods: {
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			//console.log(e)
			this.saveParams.regionId = e.cityCode;
			this.saveParams.receiverAreaName = e.label.replace('-', '').replace('-', '');
		},
		onCancel(e) {
			//console.log('取消地址选择')
		},
		switchChange (val) {
			this.saveParams.isDefault = val.detail.value ? 1 : 0
		},
		updateAddress() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.addressUpdate, that.saveParams, false).then((res) => {
				//保存成功后关闭本页面跳到上一页
				that.$turnPage('1', 'navigateBack')
			});
		},
		saveAddress() {
			if(!this.saveParams.receiver){
			  uni.showToast({
				 title: '请填写收货人',
				 icon: 'none',
				 duration: 2000
			  });
			  return
			}
			if(!this.saveParams.receiverPhone){
			  uni.showToast({
				 title: '请填写收货人手机号码',
				 icon: 'none',
				 duration: 2000
			  });
			  return
			}
			if(!this.saveParams.receiverAddr){
			  uni.showToast({
				 title: '请填写详细地址',
				 icon: 'none',
				 duration: 2000
			  });
			  return
			}
			if(this.saveParams.addressId){
			  this.updateAddress()
			  return;
			}
			let that = this;
			interfaceurl.checkAuth(interfaceurl.addressCreate, that.saveParams, false).then((res) => {
				//保存成功后关闭本页面跳到上一页
				that.$turnPage('1', 'navigateBack')
			});
		}
	}
}
